import { ModuleWithProviders } from '@angular/core'
import { RouterModule, Routes } from '@angular/router'

import { RegistrationDetailsComponent } from '../registration-details/registration-details.component'
import { LoginComponent } from '../login/login.component'
import { DiscussionFormComponent } from '../discussion-form/discussion-form.component'
import { WelcomePageComponent } from '../welcome-page/welcome-page.component'
import { AdminPanelComponent } from '../admin-panel/admin-panel.component'
import { QueryDataComponent } from '../query-data/query-data.component'

const routerDetails: Routes = [
    {
        path: '', redirectTo: 'login', pathMatch: 'full'
    },
    {
        path: 'registration', component: RegistrationDetailsComponent
    },
    {
        path: 'login', component: LoginComponent
    },
    {
        path: 'discussionForm', component: DiscussionFormComponent
    },
    {
        path: 'welcomeForm', component: WelcomePageComponent
    },
    {
        path: 'adminPanel', component: AdminPanelComponent
    },
    {
        path: 'QueryData', component:QueryDataComponent
    }
];

export const router: ModuleWithProviders = RouterModule.forRoot(routerDetails);


