export class registration {
  constructor(
    public id: string,
    public projectID: string,
    public phoneNumber: string,
    public email: string,
    public password: string,
    public confirmPassword: string
  ) { }
}

export class userDetails {
  constructor(
    public userID: string,
    public projectID: string,
    public phoneNumber: string,
    public email: string,
    public password: string,
    public confirmPassword: string,
    public isActive: number
  ) { }
}

export class login {
  constructor(
    public email: string,
    public password: string
  ) { }
}


