import { Component, OnInit } from '@angular/core';
import { FormsModule, FormGroup, FormControl, Validators } from '@angular/forms'
import { Router } from '@angular/router'
import { login } from '../model/registrationM'

import { UserdetailsService } from '../userService/userdetails.service'
import { registration } from '../model/registrationM'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  form;
  constructor(private routes: Router, private _userService: UserdetailsService) { }
  modelLogin = new login('', '');
  show = true;
  errorMessage = "";
  ngOnInit() {
    this.form = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.pattern("[^ @]*@[^ @]*")]),
      password: new FormControl('', Validators.required)
    });
    if (localStorage.getItem('loginUser') != null) {
      return this.routes.navigate(['welcomeForm']);
    }
  }

  redirectToSignUp() {
    let aa = localStorage.getItem('loginUser');
    return this.routes.navigate(['registration']);
  }
  onSubmit() {
    if(this.form.valid){
    this._userService.loginUser(this.form.value.email, this.form.value.password).subscribe(data => {
      if (data.status == 1) {
        localStorage.setItem('loginUser', JSON.stringify(data));
        return this.routes.navigate(['welcomeForm']);
      }
      else {
        this.show = false;
        let meesage = data.error;;
        setInterval(() => { this.errorMessage = meesage; }, 2500)
      }
    });
  }
  }
}



