import { Directive, TemplateRef, ViewContainerRef, Input } from '@angular/core';

@Directive({
  selector: '[appSubIf]'
})
export class SubIfDirective {

  constructor(private templateRef: TemplateRef<boolean>, private viewContainer: ViewContainerRef) { }

  @Input() set appSubIf(condition: boolean) {
    if (condition) {
      this.viewContainer.createEmbeddedView(this.templateRef);
    }
    else {
      this.viewContainer.clear();
    }
  }

}

// custom loop directive
@Directive({
  selector: '[ngloop]'
})

export class ngloopDirective {
  constructor(private templateRef: TemplateRef<any>, private viewContainer: ViewContainerRef) { }
  @Input() set ngloop(value: number) {
    for (var index = 0; index < value; index++) {
      this.viewContainer.createEmbeddedView(this.templateRef);
    }
  }
}

@Directive({
  selector: '[ngdelay]'
})

export class ngdelayDirective {

  constructor(private templateRef: TemplateRef<any>, private viewContainer: ViewContainerRef) { }

  @Input() set ngdelay(delay: number) {
    this.viewContainer.clear();
    setTimeout(() => { this.viewContainer.createEmbeddedView(this.templateRef) }, delay);
  }
}




