import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'

import { AdminAccessServiceService } from '../userService/admin-access-service.service';

import { userDetails } from '../model/registrationM'

@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css']
})
export class AdminPanelComponent implements OnInit {
  message: string;
  objUsers: userDetails[] = null;

  constructor(private adminService: AdminAccessServiceService, private router: Router) { }

  ngOnInit() {
    this.checkAuthentication();
  }

  changeUserStatus(ID: string, status: number) {
    let localuserDetails = new userDetails(ID, '', '', '', '', '', status);
    var a = confirm('Are you sure for change status?')
    if (a) {
      this.adminService.updateUserStatus(localuserDetails).subscribe(
        result => {
          if (result.status == 1) {
            this.getUsers();
          }
        }
      );
    }
  }

  getUsers() {
    try {
      this.adminService.getRegisteredUser().subscribe(data => {
        if (data.status == 1) {
          this.objUsers = data.message;
        }
        else {
          this.message = "No data available";
        }
      });
    }
    catch (err) {
      alert(err + " Some error occur!");
    }
  }
  checkAuthentication() {
    if (localStorage.getItem('loginUser') != null) {
      if (JSON.parse(localStorage.getItem('loginUser')).message.roleType == 1) {
        this.getUsers();
      }
      else {
        return this.router.navigate(['login']);
      }
    } else {
      return this.router.navigate(['login']);
    }
  }
  logout() {
    if (localStorage.getItem('loginUser') != null) {
      localStorage.clear();
      return this.router.navigate(['login']);
    }
  }
}



