import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'

import { QueryCommentServiceService } from '../QueryCommentService/query-comment-service.service'

import { QueryCommentM } from '../model/queryCommentM'

@Component({
  selector: 'app-query-data',
  templateUrl: './query-data.component.html',
  styleUrls: ['./query-data.component.css']
})
export class QueryDataComponent implements OnInit {
  localQueryComment = new QueryCommentM('1', '', '', '', '', '', '', '', '', '', '1', '', '');
  m_Querydata = "";
  m_query = "";
  messageData = "";
  m_color="";
  constructor(private router: Router, private service: QueryCommentServiceService) { }

  ngOnInit() {
  }

  saveQuery() {

    this.localQueryComment.summary = this.m_query;
    this.localQueryComment.queryDetail = this.m_Querydata;
    this.localQueryComment.isActive = "true";
    this.localQueryComment.actionType = "1";
    this.localQueryComment.UserID = JSON.parse(localStorage.getItem('loginUser')).message.userID;
    this.service.saveQuery(this.localQueryComment).subscribe(
      result => {
        if (result.status == 1) {
          this.localQueryComment.actionType = "1";
          this.localQueryComment.QueryID = "";
          this.service.getQueryDetails(this.localQueryComment).subscribe(
            result => {
              if (result.status == 1) {
                this.m_Querydata = "";
                this.m_query = "";
                this.messageData ="Add Query done!"
                this.m_color="green";
              }
              else{
                 this.messageData ="Try again later!!!"
                 this.m_color="red";
              }
            }
          );

        }
      }
    );
  }

  goTODiscussionForm() {
    return this.router.navigate(['discussionForm']);
  }
}



