import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'

import { QueryCommentServiceService } from '../QueryCommentService/query-comment-service.service'

import { QueryCommentM } from '../model/queryCommentM'

@Component({
  selector: 'app-discussion-form',
  templateUrl: './discussion-form.component.html',
  styleUrls: ['./discussion-form.component.css']
})
export class DiscussionFormComponent implements OnInit {

  constructor(private router: Router, private service: QueryCommentServiceService) { }
  queryMain: string = "";
  queryID = "";
  commentData = "";
  isvisible = false;
  arryQueries: QueryCommentM[] = null;
  arryQueryComments: QueryCommentM[] = null;
  localQueryComment = new QueryCommentM('1', '', '', '', '', '', '', '', '', '', '1', '', '');

  ngOnInit() {
    if (localStorage.getItem('loginUser') != null) {

      this.localQueryComment.QueryID = "";
      this.localQueryComment.actionType = "1";
      this.service.getQueryDetails(this.localQueryComment).subscribe(
        result => {
          if (result.status == 1) {
            this.arryQueries = result.message;
          }
        }
      );
    }
    else {
      return this.router.navigate(['login']);
    }
  }



  logout() {
    if (localStorage.getItem('loginUser') != null) {
      localStorage.clear();
      return this.router.navigate(['login']);
    }
  }
  getQueryComments(queryID: any, query: string) {

    // remove the background color from all table in in div
    for (var index = 0; index < document.getElementById('firstChild').getElementsByTagName("td").length; index++) {
      document.getElementById('firstChild').getElementsByTagName("td")[index].style.backgroundColor = "";
    }

    this.queryID = queryID;
    this.localQueryComment.QueryID = queryID;
    this.localQueryComment.actionType = "2";
    this.service.getQueryDetails(this.localQueryComment).subscribe(
      result => {
        if (result.status == 1) {
          this.queryMain = query;
          this.isvisible = true;
          this.arryQueryComments = result.message;
          document.getElementById(this.queryID).style.backgroundColor = 'green';
        }
      }
    );
  }

  saveQueryComments(action: string) {

    this.localQueryComment.QueryID = this.queryID;
    this.localQueryComment.actionType = action;
    this.localQueryComment.commentID = "1";
    this.localQueryComment.commentData = this.commentData;
    debugger;
    this.localQueryComment.commentUserID = JSON.parse(localStorage.getItem('loginUser')).message.userID;
    this.service.saveQueryComment(this.localQueryComment).subscribe(
      result => {
        if (result.status == 1) {
          debugger;
          this.arryQueryComments = result.message;
          this.commentData = "";
        }
      }
    );
  }
  goTOQueryData() {
    return this.router.navigate(['QueryData']);
  }

}



