import { Component, OnInit } from '@angular/core';
import { FormsModule, FormGroup } from '@angular/forms'
import { Router } from '@angular/router'

// Providers
import { UserdetailsService } from '../userService/userdetails.service'
import { registration } from '../model/registrationM'

@Component({
  selector: 'app-registration-details',
  templateUrl: './registration-details.component.html',
  styleUrls: ['./registration-details.component.css'],
  providers: [UserdetailsService]

})
export class RegistrationDetailsComponent implements OnInit {

  constructor(private _userService: UserdetailsService, private router: Router) { }
  allProduct: Countries[] = [];
  message: string = '';
  colorM: string = 'red';
  modelRegistration = new registration('1', '1', '', '', '', '');
  projectNames = ['SRS', 'Power Plan', 'Dymaxium'];
  dummyValue: string = "";
  ngOnInit() {
    this._userService.getUsersDetails().subscribe(
      (data: Countries[]) => { this.allProduct = data; console.log(data); },
      err => console.log('Something went wrong!')
    );
    // this._userService.getDataFromLocalAPI().subscribe((data) => (console.log(data)))
  }
  onSubmit(data) {
    debugger;
    if (data.password == data.confirmPassword) {
      this._userService.saveRegistrationDetails(data).subscribe(data => {
        if (data.status == 1) {
          this.colorM = 'green';
          this.message = "Registration done,redirect to Login Page!";
          this.modelRegistration = new registration('1', '1', '', '', '', '');
          setTimeout(() => {
            this.router.navigate(["login"]);
          }, 2500);
        }
        else {
          this.colorM = 'red';
          this.message = "Some technical error occur!"
        }
      });

    }
    else {
      this.message = "Password mismatch!!"
      this.colorM = 'red';
      setTimeout(() => { this.message = "" }, 5000);
    }
  }

  redirectToLogin() {
    return this.router.navigate(['login']);
  }

  isNumberKey(evt) {
    var charCode = (evt.which) ? evt.which : evt.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57))
      return false;
    return true;
  }
}



interface Countries {
  isocode: string;
  country: string;
}


