import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router'

// Providers
import { UserdetailsService } from '../app/userService/userdetails.service';
import { AdminAccessServiceService } from '../app/userService/admin-access-service.service';
import { QueryCommentServiceService } from '../app/QueryCommentService/query-comment-service.service';

import { router } from './PagingRouter/PageList'
import { AppComponent } from './app.component';
import { RegistrationDetailsComponent } from './registration-details/registration-details.component';
import { LoginComponent } from './login/login.component';
import { DiscussionFormComponent } from './discussion-form/discussion-form.component';
import { WelcomePageComponent } from './welcome-page/welcome-page.component';
import { AdminPanelComponent } from './admin-panel/admin-panel.component';
import { HeaderComponent } from './masterPage/header/header.component';
import { QueryDataComponent } from './query-data/query-data.component';
import { ProjectsDirective } from './directory/projects.directive';
import { SubIfDirective,ngloopDirective,ngdelayDirective } from './directory/common.directive';
import { DummyComponent } from './dummy/dummy.component';

import {CKEditorModule} from 'ng2-ckeditor/ng2-ckeditor';


@NgModule({
  declarations: [
    AppComponent,
    RegistrationDetailsComponent,
    LoginComponent,
    DiscussionFormComponent,
    WelcomePageComponent,
    AdminPanelComponent,
    HeaderComponent,
    QueryDataComponent,
    ProjectsDirective,
    SubIfDirective,
    ngloopDirective,
    ngdelayDirective,
    DummyComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    router,
    CKEditorModule
  ],
  providers: [UserdetailsService, AdminAccessServiceService,QueryCommentServiceService],
  bootstrap: [AppComponent]
})
export class AppModule { }



