import { Injectable } from '@angular/core';
import { Http, Response, Headers, URLSearchParams, RequestOptions } from '@angular/http';
//import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx'

import { registration,userDetails } from '../model/registrationM'

@Injectable()
export class AdminAccessServiceService {

  constructor(private _http: Http) { }
  getRegisteredUser() {
    return this._http.get('http://localhost:29084/Admin/GetUsers').map((res: Response) => res.json());
  }

  updateUserStatus(localObject:userDetails) {
    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    let urlSearchParams = new URLSearchParams();
    urlSearchParams.append('userID', localObject.userID);
    urlSearchParams.append('isActive', localObject.isActive.toString());

    let body = urlSearchParams.toString();
    return this._http.post("http://localhost:29084/Admin/UpdateUserStatus", body, { headers: headers }).map((res: Response) => res.json());
  }
}



